html,
body {
  height: 100%;
}

#home {
  height: 100%;
  text-align: center;
  margin-top: 5%;
}

#title {
  text-decoration: none;
  color: #95adb6;
  font-family: "Verdana";
  font-weight: bold;
  font-size: 2.8vw;
  transition: all 500ms;
}

#title:hover {
  color: #5e7999;
}

#quote {
  background-color: #d4b39e;
  margin-top: 1%;
  margin-left: 27.5%;
  margin-right: 27.5%;
  margin-bottom: 5%;
  height: 100%;
  text-align: center;
  padding: 5% 1% 5% 1%;
  border-radius: 10px;
  position: relative;
}

.home-button {
  background-color: #95adb6;
  color: white;
  height: 1%;
  width: 5%;
  padding: 0.5%;
  font-size: 0.9vw;
  font-weight: bold;
  border-radius: 5px;
  border: none;
  margin-left: 0.3%;
  transition: all 500ms;
}

#select {
  border-radius: 5px;
  margin-right: 0.3%;
  padding-left: 0.3%;
  line-height: 25px;
  height: 40px;
  background-color: #95adb6;
  color: white;
  font-size: 0.9vw;
  font-weight: bold;
  transition: all 500ms;
}

#select:hover {
  background-color: #5e7999;
}

.home-button:hover {
  background-color: #5e7999;
}

p {
  font-size: 1.2vw;
  color: white;
  font-family: "Verdana";
  margin-left: 2.5%;
  margin-right: 2.5%;
  text-align: center;
}

#author-input {
  height: 34px;
  width: 28%;
  border-radius: 5px;
  padding-left: 10px;
  margin-top: 1.5%;
  margin-bottom: 1.5%;
  border: 2px solid #95adb6;
}

::placeholder {
  color: grey;
}

.quote-line {
  border: 2px solid white;
  width: 20%;
}

.menu-button {
  width: 2.5%;
  height: 4.2%;
  position: absolute;
}

#main-menu-button {
  top: 13.9%;
  right: 27.65%;
}

#menu-home-button {
  top: 13.9%;
  right: 24.85%;
}

#menu-favorites-button {
  top: 18.5%;
  right: 24.85%;
}

#quote-favorite-button {
  background-color: #d4b39e;
  border: none;
  color: white;
}
